<template>
<div>
	<div class="page-header">
		<mt-header class="container" title="Pending Equipment Repair List">
			<span slot="left" @click="onShowMenu">
				<font-awesome-icon icon="bars" size="2x" />
			</span>
			<span slot="right" @click="popupEquipment.visible = true">
				<font-awesome-icon icon="plus" size="2x" />
			</span>
		</mt-header>
	</div>

	<div class="page-body container">
		<div v-for="item in listArray" :key="item.EqpRepairPK" class="wrapper">
			<mt-cell>
				<span slot="title">
					<span class="job">{{item.JobNumber}}</span>
					<br />
					<span class="name">{{item.EquipmentName + '(' + item.SerialNumb + ')'}}</span>
					<br />
					<span class="status">{{item.RepairStatusView}}</span>
				</span>

				<mt-button icon="more" @click="onDetail(item.EqpRepairPK)"></mt-button>
			</mt-cell>
		</div>

		<mt-popup class="equipment" v-model="popupEquipment.visible">
			<mt-field label="Job Number"  placeholder="input job number" type="text" v-model="popupEquipment.jobNumber"></mt-field>

			<div class="button-group">			
			<mt-button size="large" type="primary" @click="onSearch"><font-awesome-icon icon="search"></font-awesome-icon></mt-button>
			<mt-button size="large" type="default" @click="onSearchCancel"><font-awesome-icon icon="times"></font-awesome-icon></mt-button>	
			</div>

			<div v-for="item in popupEquipment.listArray" :key="item.EqpEquipmentPK">
				<mt-cell>
					<span slot="title">
						<span>{{item.EquipmentType}}</span>
						<span>{{item.EquipmentName}}</span>
						<span>{{item.SerialNumb}}</span>
					</span>
					<span @click="onUseEquipment(item.EqpEquipmentPK)"><font-awesome-icon icon="plus"></font-awesome-icon></span>
				</mt-cell>
			</div>	
		</mt-popup>

		<mt-popup class="submit" v-model="popupSubmit.visible">

			<div class="comments">
				<h5>Comments</h5>
				<mt-field type="textarea" rows="5" v-model="popupSubmit.comments"></mt-field>
			</div>

			<div class="photo">
				<h5>Photo</h5>
				<img v-if="popupSubmit.photo.length > 0" :src="'data:image/jpeg;base64,' + popupSubmit.photo" />
				<img v-else src="../../assets/noimage.jpg" />
				<mt-button size="large" type="primary" @click="onSubmitTakePhoto"><font-awesome-icon icon="camera"></font-awesome-icon></mt-button>
				<br />
			</div>

			<mt-button size="large" type="primary" @click="onSubmit"><font-awesome-icon icon="check"></font-awesome-icon></mt-button>
			<mt-button size="large" type="default" @click="onSubmitCancel"><font-awesome-icon icon="times"></font-awesome-icon></mt-button>	
		</mt-popup>

		<mt-popup class="menu" v-model="popupMenu.visible" position="left">
			<ul>
				<li @click="onJob">
					<font-awesome-icon icon="building" />
					<span>Job</span>
				</li>
				<li @click="onRegion">
					<font-awesome-icon icon="city" />
					<span>Region</span>
				</li>
				<li @click="onLogout">
					<font-awesome-icon icon="sign-out-alt" />
					<span>Louout</span>
				</li>
			</ul>
		</mt-popup>
	</div>
</div>
</template>

<script>
import BizRCHub from '../../api/BizRCHub.js'
import Utils from '../../utils/index.js';

export default {
	name: 'EquipmentRepairList',
	data(){
		return {
			listArray: [],
			manager: {},

			popupEquipment:{
				jobNumber: '',
				listArray: [],
				visible: false,
			},

			popupSubmit:{
				EqpEquipmentPK: '',
				comments: '',
				photo: '',
				visible: false
			},
			
			popupMenu:{
				visible: false
			}
		}
	},
	methods:{
		onSearch()
		{
			this.$showLoading();
			BizRCHub.GetEquipmentListByJobNumber(this.popupEquipment.jobNumber).then(res=>{
				if(res.data.OK)
				{
					this.popupEquipment.listArray = res.data.Result;
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		onSearchCancel()
		{
			this.popupEquipment.visible = false;
		},
		onUseEquipment(EqpEquipmentPK)
		{
			this.popupEquipment.visible = false;

			this.popupSubmit.EqpEquipmentPK = EqpEquipmentPK;
			this.popupSubmit.visible = true;
		},
		onSubmitTakePhoto()
		{
			this.$takeCameraPicture((imgData)=>{
				this.popupSubmit.photo = imgData;
			});
		},
		onSubmit()
		{
			if(this.popupSubmit.comments.length == 0)
			{
				this.$alert('The comments can not be empty.');
				return;
			}

			this.popupSubmit.visible = false;
			
			this.$showLoading();
			BizRCHub.SubmitEquipmentRepair(this.popupSubmit.EqpEquipmentPK, this.popupSubmit.comments, this.popupSubmit.photo).then(res=>{
				this.$closeLoading();
				if(res.data.OK)
				{
					let item = res.data.Result;
					this.listArray.push(item);
					this.$store.commit('setEquipmentRepairList', this.listArray);
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});	
		},
		onSubmitCancel()
		{
			this.popupSubmit.visible = false;
		},
		onDetail(EqpRepairPK) 
		{
			this.$router.push({name: 'EquipmentRepairDetail', params: {PK: EqpRepairPK}});
		},
		getList()
		{
			this.$showLoading();
			BizRCHub.GetEquipmentRepairPendingList().then(res=>{
				this.$closeLoading();
				if(res.data.OK)
				{
					this.listArray = res.data.Result;
					this.$store.commit('setEquipmentRepairList', this.listArray);
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});	
		},
		onShowMenu()
		{
			this.popupMenu.visible = true;
		},
		onRegion()
		{
			this.$router.push({name: 'RegionTimesheetList'});
		},
		onJob()
		{
			this.$router.push({name: 'JobTimesheetList'});
		},
		onLogout()
		{
			Utils.logout();
			this.$router.push({ name: 'Login' });
		}
	},
	mounted()
	{
		this.manager = Utils.getUserSettings();
		if(this.$store.state.equipmentRepairList.length > 0)
		{
			this.listArray = this.$store.state.equipmentRepairList;
		}
		else
		{
			this.getList();
		}	
	}
}
</script>

<style scoped>
	.page-header {
		background: #a9362d;
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 100;
	}
	.mint-header {
		background: #a9362d;
	}
	.menu {
		width: 260px;
		height: 100%;
		background: #c45050;
	}
	ul{
    	list-style: none;
		color: #fff;
		padding: 20px;
	}
	ul li {
		padding: 20px 0;
		border-bottom: 1px solid rgba(255,255,255,0.2);
		transition: all ease 0.3s;
	}
	ul li:hover {
		border-bottom-color: #fff;
	}
	.menu svg {
		margin-right: 10px;
	}
	.mint-header {
		line-height: normal;
	}
	.equipment {
		width: 80%;
		padding: 5px 10px 10px;
	}
	.button-group .mint-button--large {
		display: inline-block;		
		width: 50%;
	}
	.mint-button--primary {
		background-color: #a9362d;
	}
	.page-body{
		margin-bottom: 38px;
		margin-top: 40px;
	}
	.job, .status {
		color: #8e8e93;
	}
	.name {
		line-height: 1.5;
	}
	.wrapper {
		border-bottom: 1px solid #ddd;
		padding: 10px 0;
	}
</style>
