<template>
<div>
	<div class="page-header">
		<mt-header class="container" title="Equipment Repair Detail">
			<router-link :to="{name: 'EquipmentRepairList'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
			<mt-button icon="more" @click="onMore" slot="right"></mt-button>
		</mt-header>
	</div>
	
	<div class="page-body container equipment-detail">
		<mt-field disabled label="Job#" type="text" readonly="readonly" v-model="entity.JobNumber"></mt-field>
		<mt-field disabled label="Name" type="text" readonly="readonly" v-model="entity.EquipmentName"></mt-field>
	    <mt-field disabled label="Serial#" type="text" readonly="readonly" v-model="entity.SerialNumb"></mt-field>
	
	   <div v-if="entity.RepairComments.length > 0" class="comments">
			<h5>Comments</h5>
			<div v-for="item in entity.RepairComments.split(';')" :key="item">
				<p>{{item}}</p>
			</div>	
		</div>

		<div v-for="item in entity.RepairAttached" :key="item.EqpRepairAttachedPK">
			<div class="attached" v-if="item.Attached.length > 0">
				<h5>{{item.FileName}}</h5>
				<img :src="'data:image/jpeg;base64,' + item.Attached" />
			</div>
		</div>
	</div>

	<mt-popup class="functions" v-model="popupFunctions.visible">
		<div class="comments">
			<h5>Comments</h5>
			<mt-field type="textarea" rows="5" v-model="popupFunctions.comments"></mt-field>
		</div>

		<div class="photo">
			<h5>Photo</h5>
			<img v-if="popupFunctions.photo.length > 0" :src="'data:image/jpeg;base64,' + popupFunctions.photo" />
			<img v-else src="../../assets/noimage.jpg" />
			<mt-button size="large" type="primary" @click="onFunctionsTakePhoto"><font-awesome-icon icon="camera"></font-awesome-icon></mt-button>
			<br />
		</div>

		<div class="button-group">
		<mt-button size="large" type="primary" @click="onFunctions"><font-awesome-icon icon="check"></font-awesome-icon></mt-button>
		<mt-button size="large" type="default" @click="onFunctionsCancel"><font-awesome-icon icon="times"></font-awesome-icon></mt-button>	
		</div>
	</mt-popup>

	<mt-actionsheet
		:actions="command.actions"
		v-model="command.visible"
		cancelText = "Cancel">
	</mt-actionsheet>

</div>
</template>

<script>
import BizRCHub from '../../api/BizRCHub.js'
import Utils from '../../utils/index.js';

export default {
	name: 'EquipmentRepairDetail',
	data(){
		return {
			entity: {
				RepairComments:''
			},
			command:{
				actions:[],
				visible: false
			},
			popupFunctions:{
				functionId: 0,
				comments: '',
				photo: '',
				visible: false
			},
		}
	},
	methods:{
		onMore()
		{
			this.command.visible = true;
		},
		pickedUp()
		{
			this.command.visible = false;
			this.popupFunctions.functionId = 0;
			this.popupFunctions.visible = true;
		},
		returned()
		{
			this.command.visible = false;
			this.popupFunctions.functionId = 1;
			this.popupFunctions.visible = true;
		},
		onFunctionsTakePhoto()
		{
			this.$takeCameraPicture((imgData)=>{
				this.popupFunctions.photo = imgData;
			});
		},
		onFunctions()
		{
			this.popupFunctions.visible = false;
			
			this.$showLoading();
			switch(this.popupFunctions.functionId)
			{
				case 0:
				{
					BizRCHub.PickedUpEquipmentRepair(this.entity.EqpRepairPK, this.popupFunctions.comments, this.popupFunctions.photo).then(res=>{
						this.$closeLoading();
						if(res.data.OK)
						{
							let data = res.data.Result;
							for(let item of this.$store.state.equipmentRepairList)
							{
								if(item.EqpRepairPK == this.entity.EqpRepairPK)
								{
									Object.assign(item, data);
									break;
								}
							}	

							this.$router.push({name: 'EquipmentRepairList'});
						}
						else
						{
							this.$alert(res.data.Error, 'Error');
							console.log(res.data.Error);
						}
					}).catch(err=>{
						this.showNetworkError();
						console.log(err);
						this.$closeLoading();
					});	
					break;
				}
				case 1:
				{
					BizRCHub.ReturnedEquipmentRepair(this.entity.EqpRepairPK, this.popupFunctions.comments, this.popupFunctions.photo).then(res=>{
						this.$closeLoading();
						if(res.data.OK)
						{
							let data = res.data.Result;
							let list = this.$store.state.equipmentRepairList;
							for(let i = list.length - 1;i >= 0; i--)
							{
								if(list[i].EqpRepairPK == data.EqpRepairPK)
								{
									list.splice(i, 1);
									break;
								}
							}	

							this.$router.push({name: 'EquipmentRepairList'});
						}
						else
						{
							this.$alert(res.data.Error, 'Error');
							console.log(res.data.Error);
						}
					}).catch(err=>{
						this.showNetworkError();
						console.log(err);
						this.$closeLoading();
					});	
					break;
				}
			}
		},
		onFunctionsCancel()
		{
			this.popupFunctions.visible = false;
		},
	},
	mounted()
	{
		let PK = this.$route.params.PK;
		this.$showLoading();
		BizRCHub.GetEquipmentRepair(PK).then(res=>{
			if(res.data.OK)
			{
				this.entity = res.data.Result;
			}
			else
			{
				this.$alert(res.data.Error, 'Error');
				console.log(res.data.Error);
			}
			
			this.$closeLoading();
		}).catch(err=>{
			this.showNetworkError();
			console.log(err);
			this.$closeLoading();
		});

		this.command.actions.push({name: 'Picked-Up', method: this.pickedUp});
		this.command.actions.push({name: 'Returned', method: this.returned});
	}
}
</script>

<style scoped>
	.page-header {
		background: #a9362d;
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 100;
	}
	.mint-header {
		background: #a9362d;
	}
	.page-body{
		margin-bottom: 38px;
		margin-top: 40px;
	}
	.page-body h5 {
		font-size: 16px;
		font-weight: normal;
		padding-left: 10px;
	}
	.page-body p {
		padding-left: 20px;
	}
	.page-body img {
		padding-left: 20px;
	}
	.comments, .attached, .photo {
		text-align: center;
	}
	.functions{
		width:80%;
		padding: 10px;
	}
	.functions .mint-button--primary {
		background-color: #a9362d;
	}
	.submit {
		width: 80%;
		padding: 10px;
	}
</style>
<style>
	.equipment-detail .mint-field-core {
		background-color: #fff;
	}
</style>